<template>
   <div style="width:300px;margin:100px auto">
      <img src="./../assets/sr.png" width="200px" style="margin:20px auto" />

      <Form
         ref="formInline"
         :model="formInline"
         style="width:290px"
         :rules="ruleInline"
      >
         <div>
            <FormItem prop="user">
               <Input
                  type="text"
                  v-model="formInline.user"
                  placeholder="اسم المستخدم"
               >
                  <Icon type="ios-person-outline" slot="prepend"></Icon>
               </Input>
            </FormItem>
         </div>
         <div>
            <FormItem prop="password">
               <Input
                  type="password"
                  v-model="formInline.password"
                  placeholder="كلمة المرور"
               >
                  <Icon type="ios-lock-outline" slot="prepend"></Icon>
               </Input>
            </FormItem>
         </div>
         <div>
            <FormItem>
               <Button type="primary" @click="handleSubmit('formInline')"
                  >تسجيل دخول</Button
               >
            </FormItem>
         </div>
      </Form>
   </div>
</template>

<script>
export default {
   data() {
      return {
         formInline: {
            user: "",
            password: "",
         },
         ruleInline: {
            user: [
               {
                  required: true,
                  message: "الرجاء ادخال اسم المستخدم",
                  trigger: "blur",
               },
            ],
            password: [
               {
                  required: true,
                  message: "الرجاء ادخال كلمة المرور",
                  trigger: "blur",
               },
               {
                  type: "string",
                  message: "كلمة المرور غير صحيحة",
                  trigger: "blur",
               },
            ],
         },
      };
   },
   methods: {
      handleSubmit(name) {
         this.$refs[name].validate((valid) => {
            console.log(valid);
            if (valid) {
               this.$http
                  .post(`http://invoice.srit.me/login`, this.formInline)
                  .then((res) => {
                     console.log(res.data);
                     this.$router.go(0);
                     localStorage.setItem(
                        "loggedIn",
                        JSON.stringify(res.data[0])
                     );
                     this.$Message.success("تم تسجيل الدخول بنجاح");
                  })
                  .catch((e) => {
                     console.log(e);
                     this.$Message.error("المعلومات المدخلة غير صحيحة");
                  });
            } else {
               this.$Message.error("المعلومات المدخلة غير صحيحة");
            }
         });
      },
   },
};
</script>

<style lang="scss">
.ivu-input-group > .ivu-input:last-child {
   direction: rtl;
}
.ivu-form-item-error-tip {
   right: 0 !important;
}
</style>
