<template>
   <div :key="keyComponent">
      <Row style="margin:20px;direction:rtl">
         <Col span="6">
            <Select
               v-model="selectedFindProvince"
               placeholder="المحافظة"
               filterable
               clearable
               @on-clear="clearData"
               style="width:200px"
               @on-change="findInvoiceByProvinceId"
            >
               <Option
                  v-for="(province, index) in allProvince"
                  :value="province.idProvince"
                  :key="index"
                  >{{ province.provinceName }}</Option
               >
            </Select>
         </Col>
         <Col span="6">
            <Select
               v-model="selectedFindDriver"
               placeholder="السائق"
               filterable
               clearable
               style="width:200px"
            >
               <Option
                  v-for="(driver, index) in allDrivers"
                  :value="driver.idDriver"
                  :key="index"
                  >{{ driver.driverName }}</Option
               >
            </Select>
         </Col>
         <Col span="6">
            <Button
               type="primary"
               shape="circle"
               icon="md-done-all"
               @click="setDriverForBills"
            >
               حفظ
            </Button>
         </Col>
         <Col span="6">
            <Button
               shape="circle"
               style="background:#123456;color:#fff"
               icon="ios-print"
               @click="printInvoice"
            >
               طباعة الفاتورة
            </Button>
         </Col>
      </Row>
      <div id="table-data">
         <Table
            style="direction:rtl"
            border
            ref="selection"
            :columns="columns4"
            :data="allBills"
            @on-select="selectedRow"
            @on-select-cancel="deSelectRow"
         ></Table>
      </div>
   </div>
</template>

<script>
export default {
   data() {
      return {
         columns4: [
            {
               type: "selection",
               width: 60,
               align: "center",
            },
            {
               title: "اسم الزبون",
               key: "customerName",
            },
            {
               title: "عنوان الزبون",
               key: "address",
            },
            {
               title: "هاتف الزبون",
               key: "customerPhone",
            },
            {
               title: "المحل او البيج",
               key: "storeName",
            },
            {
               title: "المحافظة",
               key: "provinceName",
            },
            {
               title: "المبلغ",
               key: "totalPrice",
            },
         ],

         allBills: [],
         allDrivers: [],
         allStores: [],
         originalBills: [],
         originalDrivers: [],
         originalStores: [],
         allProvince: [],
         originalProvince: [],
         selectedBills: [],
         driverData: {},
         selectedFindDriver: "",
         selectedFindBill: "",
         selectedFindStore: "",
         selectedFindProvince: "",
         customerName: "",
         totalPrice: "",
         customerPhone: "",
         address: "",
         invoiceDate: "",
         note: "",
         selectedStore: 0,
         addInvoice: false,
         value: "",
         selectedDriver: 0,
         selectedAddDriver: 0,
         selectedAddStore: 0,
         keyComponent: 0,
      };
   },
   created() {
      let self = this;
      self.getAllData();
   },
   methods: {
      getAllData() {
         let self = this;

         let drivers = `http://invoice.srit.me/drivers`;
         let bills = `http://invoice.srit.me/driverBills`;
         let province = `http://invoice.srit.me/allProvince`;
         let stores = `http://invoice.srit.me/allStores`;

         function requestDrivers() {
            return self.$http.get(drivers);
         }

         function requestBills() {
            return self.$http.get(bills);
         }

         function requestStores() {
            return self.$http.get(stores);
         }
         function requestProvince() {
            return self.$http.get(province);
         }

         Promise.allSettled([
            requestDrivers(),
            requestBills(),
            requestStores(),
            requestProvince(),
         ])
            .then((responses) => {
               const responseOne = responses[0];
               const responseTwo = responses[1];
               const responseThree = responses[2];
               const responseFour = responses[3];
               console.log(responseOne);

               self.allDrivers =
                  responseOne.status == "fulfilled"
                     ? responseOne.value.data
                     : [];
               self.originalDrivers =
                  responseOne.status == "fulfilled"
                     ? responseOne.value.data
                     : [];
               self.allBills =
                  responseTwo.status == "fulfilled"
                     ? responseTwo.value.data
                     : [];
               self.originalBills =
                  responseTwo.status == "fulfilled"
                     ? responseTwo.value.data
                     : [];
               self.allStores =
                  responseThree.status == "fulfilled"
                     ? responseThree.value.data
                     : [];
               self.originalStores =
                  responseThree.status == "fulfilled"
                     ? responseThree.value.data
                     : [];
               self.allProvince =
                  responseFour.status == "fulfilled"
                     ? responseFour.value.data
                     : [];
               self.originalProvince =
                  responseFour.status == "fulfilled"
                     ? responseFour.value.data
                     : [];

               console.log(self.allDrivers);
               console.log(self.allStores);
               console.log(self.allBills);
            })
            .catch((errors) => {
               console.log(errors);
            });
      },
      selectedRow(selection) {
         let self = this;
         self.selectedBills = selection;
      },
      deSelectRow(selection) {
         console.log(selection);
      },
      setDriverForBills() {
         let self = this;
         if (self.selectedBills.length > 0 && self.selectedFindDriver) {
            self.driverData = {
               driverId: self.selectedFindDriver,
               driverBill: self.selectedBills,
            };
            self.$Message.success("تم تعيين السائق");
         } else {
            self.$Message.error(" الرجاء اختيار سائق وتحديد الفواتير");
         }
      },
      printInvoice() {
         let self = this;
         if (self.driverData.hasOwnProperty("driverId")) {
            this.$http
               .put("http://invoice.srit.me/setDriverBill", self.driverData)
               .then((res) => {
                  console.log(res.data);
                  self.$Message["success"]({
                     background: true,
                     content: "تم حفظ البيانات",
                  });
               })
               .catch((e) => {
                  console.log(e);
                  self.$Message["error"]({
                     background: true,
                     content: "حدث خطأ اثناء حفظ البيانات تأكد من المعلومات",
                  });
               });
            this.$store.state.printData = self.driverData;
            this.$router.push("/print-invoice");
         } else {
            self.$Message.error(" الرجاء اختيار سائق وتحديد الفواتير");
         }
      },
      findInvoiceByNo() {
         let self = this;

         let matchNumber;

         matchNumber = self.allBills.find((bill) => {
            return bill.billNo == self.selectedFindBill;
         });
         if (matchNumber) {
            self.allBills = [matchNumber];
         } else {
            self.allBills = self.originalBills;
         }
      },

      findInvoiceByStoreName() {
         let self = this;

         let matchName = self.allBills.filter((bill) => {
            return bill.storeId == self.selectedFindStore;
         });

         if (matchName) {
            self.allBills = matchName;
         } else {
            self.allBills = self.originalBills;
         }
      },
      findInvoiceByDriverId() {
         let self = this;

         let matchName = self.allBills.filter((bill) => {
            return bill.driverId == self.selectedFindDriver;
         });

         if (matchName) {
            self.allBills = matchName;
         } else {
            self.allBills = self.originalBills;
         }
      },
      findInvoiceByProvinceId() {
         let self = this;

         let matchName = self.allBills.filter((bill) => {
            return bill.provinceId == self.selectedFindProvince;
         });

         if (matchName) {
            self.allBills = matchName;
         } else {
            self.allBills = self.originalBills;
         }
      },
      findInvoiceByDate() {
         let self = this;

         let utcDate = new Date(self.invoiceDate).toISOString();

         console.log(utcDate);

         let matchName = self.allBills.filter((bill) => {
            return bill.billDate == utcDate;
         });

         if (matchName) {
            self.allBills = matchName;
         } else {
            self.allBills = self.originalBills;
         }
      },
      clearData() {
         let self = this;
         self.allBills = self.originalBills;
         self.keyComponent += 1;
      },
   },
};
</script>

<style lang="scss" scoped></style>
