<template>
   <div class="home" :key="keyComponent">
      <Row style="margin:20px">
         <Col span="4" style="padding-right:10px">
            <Select
               v-model="selectedFindBill"
               filterable
               clearable
               style="width:200px"
               placeholder="رقم الفاتورة"
               @on-change="findInvoiceByNo"
            >
               <Option
                  v-for="(bill, index) in allBills"
                  :value="bill.billNo"
                  :key="index"
                  >{{ bill.billNo }}</Option
               >
            </Select>
         </Col>
         <Col span="4" style="padding-right:10px">
            <Select
               v-model="selectedFindStore"
               placeholder="اسم المحل او الصفحة"
               filterable
               clearable
               @on-clear="clearData"
               style="width:200px"
               @on-change="findInvoiceByStoreName"
            >
               <Option
                  v-for="(store, index) in allStores"
                  :value="store.idStore"
                  :key="index"
                  >{{ store.storeName }}</Option
               >
            </Select>
         </Col>
         <Col span="4">
            <Select
               v-model="selectedFindProvince"
               placeholder="المحافظة"
               filterable
               clearable
               @on-clear="clearData"
               style="width:200px"
               @on-change="findInvoiceByProvinceId"
            >
               <Option
                  v-for="(province, index) in allProvince"
                  :value="province.idProvince"
                  :key="index"
                  >{{ province.provinceName }}</Option
               >
            </Select>
         </Col>
         <Col span="4">
            <Select
               v-model="selectedFindStatus"
               placeholder="الحالة"
               filterable
               clearable
               @on-clear="clearData"
               style="width:200px"
               @on-change="findInvoiceByStatusId"
            >
               <Option
                  v-for="(status, index) in statusData"
                  :value="status.statusId"
                  :key="index"
                  >{{ status.statusName }}</Option
               >
            </Select>
         </Col>
         <Col span="4">
            <DatePicker
               v-model="invoiceDate"
               type="date"
               placeholder="اختر يوم"
               style="width: 200px"
               @on-clear="clearData"
               @on-change="findInvoiceByDate"
            ></DatePicker>
         </Col>

         <Col span="4">
            <Button
               type="success"
               shape="circle"
               icon="ios-add"
               @click="addInvoice = true"
               >اضافة فاتورة</Button
            >
         </Col>
      </Row>

      <Table
         border
         :columns="columns12"
         :data="allBills"
         :loading="loading"
         style="direction:rtl;margin:10px"
      >
         <template slot-scope="{ row }" slot="name">
            <strong>{{ row.name }}</strong>
         </template>
         <template slot-scope="{ row, index }" slot="action">
            <Button
               type="info"
               shape="circle"
               icon="ios-information-circle"
               size="small"
               @click="show(index)"
            ></Button>

            <Button
               style="background:#515a6e;color:#fff;margin:0 5px"
               shape="circle"
               icon="ios-print"
               size="small"
               @click="printBill(row)"
            ></Button>

            <Button
               type="success"
               shape="circle"
               icon="md-checkmark-circle-outline"
               size="small"
               @click="changeStatus(row)"
            ></Button>
         </template>
         <template slot-scope="{ row, index }" slot="status">
            <div style="display: inline-block;margin:1px; padding:2px">
               <Icon
                  type="ios-cube"
                  color="purple"
                  size="20"
                  v-if="row.pending == 0 && row.delivered == 0"
               />

               <Icon
                  type="ios-car"
                  color="Blue"
                  size="20"
                  v-if="
                     row.pending == 1 && row.delivered == 0 && row.returned == 0
                  "
               />

               <Icon
                  type="ios-checkmark-circle"
                  color="green"
                  size="20"
                  v-if="row.delivered == 1 && row.pending == 1"
               />

               <Icon
                  type="ios-close-circle"
                  color="red"
                  size="20"
                  v-if="
                     row.delivered == 0 && row.pending == 1 && row.returned == 1
                  "
               />
            </div>
         </template>
         <template slot-scope="{ row, index }" slot="footer"> sss</template>
      </Table>

      <Modal
         v-model="addInvoice"
         title="اضافة فاتورة"
         width="600px"
         @on-ok="ok"
         @on-cancel="cancel"
      >
         <Row style="direction:rtl;margin:10px auto">
            <Col span="12" style="text-align:center">
               <span> تاريخ الفاتورة : {{ getDate(new Date()) }} </span>
            </Col>
            <Col span="12" style="text-align:center">
               <span>رقم الفاتورة : {{ allBills.length + 1 }}</span>
            </Col>
         </Row>
         <Row style="direction:rtl">
            <Col span="12" style="text-align:center">
               <div>
                  <Input
                     v-model="customerPhone"
                     placeholder=" رقم الهاتف..."
                     style="width: 200px"
                  />
               </div>
               <div style="margin : 5px auto">
                  <Select
                     v-model="selectedAddProvince"
                     placeholder="المحافظة"
                     filterable
                     clearable
                     style="width:200px"
                  >
                     <Option
                        v-for="(province, index) in allProvince"
                        :value="province.idProvince"
                        :key="index"
                        >{{ province.provinceName }}</Option
                     >
                  </Select>
               </div>
            </Col>
            <Col span="12" style="text-align:center">
               <div>
                  <Input
                     v-model="customerName"
                     placeholder="اسم الزبون..."
                     style="width: 200px"
                  />
               </div>
               <div style="margin : 5px auto">
                  <Input
                     v-model="address"
                     placeholder="العنوان..."
                     style="width: 200px"
                  />
               </div>
               <div style="margin : 5px auto">
                  <Input
                     v-model="totalPrice"
                     placeholder="السعر الكلي..."
                     style="width: 200px"
                  />
               </div>
               <div style="margin : 5px auto">
                  <Select
                     v-model="selectedAddStore"
                     placeholder="المحل او الصفحة"
                     filterable
                     clearable
                     style="width:200px"
                  >
                     <Option
                        v-for="(store, index) in allStores"
                        :value="store.idStore"
                        :key="index"
                        >{{ store.storeName }}</Option
                     >
                  </Select>
               </div>
            </Col>
         </Row>
      </Modal>

      <Modal v-model="deliveredStatus" width="360">
         <p slot="header" style="color:#123456;text-align:center">
            <Icon type="ios-information-circle"></Icon>
            <span>حالة البضاعة</span>
         </p>
         <div style="text-align:center">
            <p>
               هل انت متأكد من ان البضاعة قد سلمت للزبون او تم استرجاعها ؟
            </p>
         </div>
         <div slot="footer">
            <Button
               type="error"
               :loading="waitToChangeStatus"
               @click="confirmReturn"
               >تم الاسترجاع</Button
            >
            <Divider type="vertical" />
            <Button
               type="success"
               :loading="waitToChangeStatus"
               @click="confirmStatus"
               >تم التسليم</Button
            >
         </div>
      </Modal>
   </div>
</template>

<script>
export default {
   name: "Home",
   data() {
      return {
         columns12: [
            {
               title: "رقم الفاتورة",
               key: "billNo",
            },
            {
               title: "اسم الزبون",
               key: "customerName",
            },
            {
               title: "عنوان الزبون",
               key: "address",
            },
            {
               title: "هاتف الزبون",
               key: "customerPhone",
            },
            {
               title: "اسم الصفحة/المحل",
               key: "storeName",
            },
            {
               title: "هاتف الصفحة/المحل",
               key: "storePhone",
            },
            {
               title: "المحافظة",
               key: "provinceName",
            },
            {
               title: "السعر",
               key: "totalPrice",
            },
            {
               title: "الحالة",
               slot: "status",
               width: 100,
               align: "center",
            },
            {
               title: "Action",
               slot: "action",
               width: 120,
               align: "center",
            },
         ],
         statusData: [
            { statusId: 1, statusName: "في المخزن" },
            { statusId: 2, statusName: "قيد التوصيل" },
            { statusId: 3, statusName: "تم التسليم" },
            { statusId: 4, statusName: "مسترجع" },
         ],
         allBills: [],
         allDrivers: [],
         allStores: [],
         originalBills: [],
         originalDrivers: [],
         originalStores: [],
         allProvince: [],
         originalProvince: [],
         selectedAddProvince: "",
         deliveredStatus: false,
         waitToChangeStatus: false,
         rowToChangeStatus: {},
         selectedFindDriver: "",
         selectedFindBill: "",
         selectedFindStatus: "",
         selectedFindProvince: "",
         selectedFindStore: "",
         customerName: "",
         totalPrice: "",
         customerPhone: "",
         address: "",
         invoiceDate: "",
         note: "",
         selectedStore: 0,
         addInvoice: false,
         loading: true,
         value: "",
         selectedDriver: 0,
         selectedAddDriver: 0,
         selectedAddStore: 0,
         keyComponent: 0,
      };
   },
   created() {
      let self = this;
      self.getAllData();
   },
   methods: {
      getDate(datetime) {
         let date = new Date(datetime);
         return (
            date.getDate() +
            " / " +
            (date.getMonth() + 1) +
            " / " +
            date.getFullYear()
         );
      },
      changeStatus(row) {
         let self = this;
         if (row.delivered == 1 && row.pending == 1) {
            self.$Message.success("لقد تم تسليم هذه البضاعة");
         } else if (row.pending == 0) {
            self.$Message.error("يجب اولا تعيين سائق للفاتورة");
         } else if (row.returned == 1) {
            self.$Message.error("تم استرجاع البضاعة");
         } else {
            this.deliveredStatus = true;
            this.rowToChangeStatus = row;
         }
      },
      confirmReturn() {
         let self = this;
         this.$http
            .put(
               `http://invoice.srit.me/updateReturn/${self.rowToChangeStatus.idBill}`,
               { returned: 1 }
            )
            .then((res) => {
               console.log(res.data);
               self.$Message["success"]({
                  background: true,
                  content: "تم تحديث الحالة الى استرجاع",
               });
               self.deliveredStatus = false;
               self.getAllData();
               self.keyComponent += 1;
            })
            .catch((e) => {
               console.log(e);
               self.$Message["error"]({
                  background: true,
                  content: "حدث خطأ اثناء تعديل الحالة تأكد من المعلومات",
               });
            });
      },
      confirmStatus() {
         let self = this;

         this.$http
            .put(
               `http://invoice.srit.me/updateDelivered/${self.rowToChangeStatus.idBill}`,
               { delivered: 1 }
            )
            .then((res) => {
               console.log(res.data);
               self.$Message["success"]({
                  background: true,
                  content: "تم تحديث الحالة الى استلام",
               });
               self.deliveredStatus = false;
               self.getAllData();
               self.keyComponent += 1;
            })
            .catch((e) => {
               console.log(e);
               self.$Message["error"]({
                  background: true,
                  content: "حدث خطأ اثناء تعديل الحالة تأكد من المعلومات",
               });
            });
      },
      show(index) {
         let self = this;
         self.$Modal.info({
            title: "معلومات الفاتورة",
            content: `التاريخ：${self.getDate(
               self.allBills[index].billDate
            )}<br>الملاحظات：${
               self.allBills[index].note
            }<br>هاتف المحل/الصفحة：${self.allBills[index].storePhone}`,
         });
      },
      printBill(row) {
         console.log(row);
      },
      getAllData() {
         let self = this;

         let province = `http://invoice.srit.me/allProvince`;
         let bills = `http://invoice.srit.me/bills`;
         let stores = `http://invoice.srit.me/allStores`;

         function requestDrivers() {
            return self.$http.get(province);
         }

         function requestBills() {
            return self.$http.get(bills);
         }

         function requestStores() {
            return self.$http.get(stores);
         }

         Promise.allSettled([requestDrivers(), requestBills(), requestStores()])
            .then((responses) => {
               const responseOne = responses[0];
               const responseTwo = responses[1];
               const responseThree = responses[2];
               console.log(responseOne);
               self.loading = false;

               self.allProvince =
                  responseOne.status == "fulfilled"
                     ? responseOne.value.data
                     : [];
               self.originalProvince =
                  responseOne.status == "fulfilled"
                     ? responseOne.value.data
                     : [];
               self.allBills =
                  responseTwo.status == "fulfilled"
                     ? responseTwo.value.data
                     : [];
               self.originalBills =
                  responseTwo.status == "fulfilled"
                     ? responseTwo.value.data
                     : [];
               self.allStores =
                  responseThree.status == "fulfilled"
                     ? responseThree.value.data
                     : [];
               self.originalStores =
                  responseThree.status == "fulfilled"
                     ? responseThree.value.data
                     : [];

               console.log(self.allDrivers);
               console.log(self.allStores);
               console.log(self.allBills);
            })
            .catch((errors) => {
               console.log(errors);
            });
      },

      ok() {
         let self = this;

         if (
            self.selectedAddProvince &&
            self.selectedAddStore &&
            self.customerName &&
            self.customerPhone &&
            self.address &&
            self.totalPrice
         ) {
            let bill = {
               billNo: self.allBills.length + 1,
               driverId: 0,
               provinceId: self.selectedAddProvince,
               storeId: self.selectedAddStore,
               billInfo: [
                  {
                     customerName: self.customerName,
                     customerPhone: self.customerPhone,
                     address: self.address,
                     totalPrice: self.totalPrice,
                     note: self.note,
                  },
               ],
            };

            console.log(bill);
            this.$http
               .post("http://invoice.srit.me/addBill", bill)
               .then((res) => {
                  console.log(res.data);
                  self.$Message["success"]({
                     background: true,
                     content: "تم حفظ البيانات",
                  });
                  self.getAllData();
                  self.keyComponent += 1;
               })
               .catch((e) => {
                  console.log(e);
                  self.$Message["error"]({
                     background: true,
                     content: "حدث خطأ اثناء حفظ البيانات تأكد من المعلومات",
                  });
               });
         } else {
            self.$Message["error"]({
               background: true,
               content: "الرجاء ادخال جميع الحقول",
            });
         }
      },
      cancel() {
         this.$Message.info("Clicked cancel");
      },
      findInvoiceByProvinceId() {
         let self = this;

         let matchName = self.allBills.filter((bill) => {
            return bill.provinceId == self.selectedFindProvince;
         });

         if (matchName) {
            self.allBills = matchName;
         } else {
            self.allBills = self.originalBills;
         }
      },
      findInvoiceByNo() {
         let self = this;

         let matchNumber;

         matchNumber = self.allBills.find((bill) => {
            return bill.billNo == self.selectedFindBill;
         });
         if (matchNumber) {
            self.allBills = [matchNumber];
         } else {
            self.allBills = self.originalBills;
         }
      },

      findInvoiceByStoreName() {
         let self = this;

         let matchName = self.allBills.filter((bill) => {
            return bill.storeId == self.selectedFindStore;
         });

         if (matchName) {
            self.allBills = matchName;
         } else {
            self.allBills = self.originalBills;
         }
      },
      findInvoiceByStatusId() {
         let self = this;
         let matchName = [];

         if (self.selectedFindStatus == 1) {
            matchName = self.allBills.filter((bill) => {
               return bill.pending == 0;
            });
         } else if (self.selectedFindStatus == 2) {
            matchName = self.allBills.filter((bill) => {
               return (
                  bill.pending == 1 && bill.delivered == 0 && bill.returned == 0
               );
            });
         } else if (self.selectedFindStatus == 3) {
            matchName = self.allBills.filter((bill) => {
               return (
                  bill.pending == 1 && bill.delivered == 1 && bill.returned == 0
               );
            });
         } else if (self.selectedFindStatus == 4) {
            matchName = self.allBills.filter((bill) => {
               return (
                  bill.pending == 1 && bill.delivered == 0 && bill.returned == 1
               );
            });
         } else {
            self.allBills = self.originalBills;
         }

         if (matchName) {
            self.allBills = matchName;
         } else {
            self.allBills = self.originalBills;
         }
      },
      findInvoiceByDriverId() {
         let self = this;

         let matchName = self.allBills.filter((bill) => {
            return bill.driverId == self.selectedFindDriver;
         });

         if (matchName) {
            self.allBills = matchName;
         } else {
            self.allBills = self.originalBills;
         }
      },
      findInvoiceByDate() {
         let self = this;

         let utcDate = new Date(self.invoiceDate).toLocaleDateString();

         console.log(utcDate);

         let matchName = self.allBills.filter((bill) => {
            return new Date(bill.billDate).toLocaleDateString() == utcDate;
         });

         console.log(matchName);

         if (matchName) {
            self.allBills = matchName;
         } else {
            self.invoiceDate = "";
            self.allBills = self.originalBills;
         }
      },
      clearData() {
         let self = this;
         self.invoiceDate = "";
         self.allBills = self.originalBills;

         self.keyComponent += 1;
      },
   },
};
</script>
