<template>
   <div id="app">
      <div id="nav" style="background:#515a6e">
         <Menu mode="horizontal" theme="dark" v-if="login" active-name="1">
            <Row>
               <Col span="6">
                  <div style="display:inline-block">
                     <Button
                        icon="ios-exit"
                        style="color:#515a6e"
                        @click="logout"
                        >تسجيل خروج</Button
                     >
                  </div>
               </Col>
               <Col span="18">
                  <div style="margin-right:50px">
                     <MenuItem name="1" to="/" v-if="showBill">
                        <Icon type="ios-paper" />
                        الفواتير
                     </MenuItem>

                     <MenuItem name="4" to="/invoice" v-if="showDriverBill">
                        <Icon type="ios-list-box" />
                        فواتير التسليم
                     </MenuItem>
                     <MenuItem name="2" to="/stores">
                        <Icon type="ios-cart" />
                        المحلات / الصفحات
                     </MenuItem>

                     <MenuItem name="3" to="/drivers">
                        <Icon type="ios-people" />
                        السواق
                     </MenuItem>
                  </div>
               </Col>
            </Row>
         </Menu>
      </div>
      <router-view />
   </div>
</template>

<script>
export default {
   data() {
      return {
         login: false,
         showDriverBill: false,
         showBill: false,
         user: "",
      };
   },
   created() {
      let login = JSON.parse(localStorage.getItem("loggedIn"));
      if (login) {
         this.login = true;
         this.user = login;
         if (login.roleId == 1) {
            this.$router.push("/").catch((e) => {});
            this.showDriverBill = true;
            this.showBill = true;
         } else if (login.roleId == 2) {
            this.$router.push("/").catch((e) => {});
            this.showDriverBill = false;
            this.showBill = true;
         } else {
            this.$router.push("/invoice").catch((e) => {});
            this.showDriverBill = true;
            this.showBill = false;
         }
      } else {
         this.$router.push("/login").catch((e) => {});
      }
   },
   methods: {
      logout() {
         localStorage.removeItem("loggedIn");
         this.$router.go(0);
      },
   },
};
</script>

