<template>
   <div>
      <Row style="margin:10px">
         <Col span="8" style="padding-top:20px"
            ><span> 07833032246 </span> <br /><span> 07730089006 </span></Col
         >
         <Col span="8">
            <img src="./../assets/sr.png" alt="" width="150px" />
         </Col>
         <Col span="8" style="padding-top:30px">التاريخ : {{ getDate() }}</Col>
      </Row>
      <div style="direction:rtl;margin:10px">
         <table style="width:100%;border: 1px solid rgb(218 218 218);">
            <tr style="background:rgb(218 218 218)">
               <th>ت</th>
               <th>رقم الفاتورة</th>
               <th>العنوان</th>
               <th>اسم البيج</th>
               <th>رقم الزبون</th>
               <th>المحافظة</th>
               <th>المبلغ</th>
            </tr>
            <tr
               v-for="(bill, index) in invoice.driverBill"
               style="background:rgb(245 245 245)"
            >
               <td>{{ index + 1 }}</td>
               <td>{{ bill.billNo }}</td>
               <td>{{ bill.address }}</td>
               <td>{{ bill.storeName }}</td>
               <td>{{ bill.customerPhone }}</td>
               <td>{{ bill.provinceName }}</td>
               <td>{{ bill.totalPrice }}</td>
            </tr>
         </table>
      </div>
      <Row style="margin:10px">
         <Col span="12"> المجموع : {{ reduceTotalPrice() }}</Col>
         <Col span="12">
            الملاحظات
         </Col>
      </Row>

      <Row style="margin:10px">
         <Col span="24">
            <Button
               id="print-btn"
               shape="circle"
               style="background:#123456;color:#fff"
               icon="ios-print"
               @click="print"
            >
               طباعة الفاتورة
            </Button>
         </Col>
      </Row>
   </div>
</template>

<script>
export default {
   data() {
      return {
         invoice: {},
      };
   },
   created() {
      this.invoice = this.$store.state.printData;
      console.log(this.invoice);
   },
   methods: {
      print() {
         window.print();
      },
      getDate() {
         let date = new Date();
         return (
            date.getDate() +
            " / " +
            (date.getMonth() + 1) +
            " / " +
            date.getFullYear()
         );
      },
      reduceTotalPrice() {
         let data = this.invoice.driverBill.map((bill) => {
            return bill.totalPrice.split(",").join("") * 1;
         });

         console.log(data);

         return data.reduce((total, amount) => total + amount);
      },
   },
};
</script>

<style lang="scss">
@media print {
   @page {
      size: landscape;
   }
   #nav {
      display: none;
   }
   #print-btn {
      display: none;
   }
   body {
      -webkit-print-color-adjust: exact !important;
   }
}
