<template>
   <div style="margin:10px;direction: rtl;">
      <Row style="margin:10px 0">
         <Col style="text-align:right">
            <Button
               type="success"
               shape="circle"
               icon="ios-add"
               @click="addStore = true"
               >اضافة محل / صفحة</Button
            >
         </Col>
      </Row>
      <Table border :columns="columns12" :data="stores" :loading="loading">
         <template slot-scope="{ row }" slot="name">
            <strong>{{ row.name }}</strong>
         </template>
         <template slot-scope="{ row, index }" slot="action">
            <Button type="error" size="small" @click="remove(row)">حذف</Button>
         </template>
      </Table>

      <Modal
         v-model="addStore"
         title="اضافة محل/ صفحة"
         width="600px"
         @on-ok="ok"
         @on-cancel="cancel"
      >
         <Row style="direction:rtl">
            <Col span="24">
               <div>
                  <Input
                     v-model="addNewStore.storeName"
                     placeholder="اسم المحل / الصفحة..."
                     style="width: 200px;margin : 5px auto"
                  />
               </div>
               <div>
                  <Input
                     v-model="addNewStore.storePhone"
                     placeholder=" رقم الهاتف..."
                     style="width: 200px ;margin : 5px auto"
                  />
               </div>

               <div>
                  <Input
                     v-model="addNewStore.storeAddress"
                     placeholder="العنوان..."
                     style="width: 200px ;margin : 5px auto"
                  />
               </div>
               <div>
                  <Input
                     v-model="addNewStore.storeNote"
                     type="textarea"
                     :rows="4"
                     placeholder="الملاحظات..."
                     style="width: 200px ;margin : 5px auto"
                  />
               </div>
            </Col>
         </Row>
      </Modal>
   </div>
</template>

<script>
export default {
   data() {
      return {
         columns12: [
            {
               title: "اسم المحل / الصفحة",
               key: "storeName",
            },
            {
               title: "رقم الهاتف",
               key: "storePhone",
            },
            {
               title: "العنوان",
               key: "storeAddress",
            },
            {
               title: "الملاحظات",
               key: "storeNote",
            },
            {
               title: "Action",
               slot: "action",
               width: 150,
               align: "center",
            },
         ],
         stores: [],
         loading: true,
         addStore: false,
         addNewStore: {
            storeName: "",
            storePhone: "",
            storeAddress: "",
            storeNote: "",
         },
      };
   },
   created() {
      this.getStores();
   },
   methods: {
      getStores() {
         let self = this;
         self.loading = true;
         this.$http
            .get("http://invoice.srit.me/allStores")
            .then((res) => {
               console.log(res.data);
               self.stores = res.data;
               self.loading = false;
            })
            .catch((e) => {
               console.log(e);
            });
      },
      remove(index) {
         let self = this;
         console.log(index);
         this.$http
            .delete(`http://invoice.srit.me/deleteStore/${index.idStore}`)
            .then((res) => {
               self.$Message["success"]({
                  background: true,
                  content: "تم حذف المحل او الصفحة",
               });
               this.getStores();
            })
            .catch((e) => {
               self.$Message["error"]({
                  background: true,
                  content: "حدث خطأ لم يتم حذف المحل او الصفحة",
               });
            });
      },
      ok() {
         let self = this;
         if (
            this.addNewStore.storeName &&
            this.addNewStore.storePhone &&
            this.addNewStore.storeAddress &&
            this.addNewStore.storeNote
         ) {
            this.$http
               .post("http://invoice.srit.me/addStore", this.addNewStore)
               .then((res) => {
                  self.$Message["success"]({
                     background: true,
                     content: "تم حفظ البيانات",
                  });
                  this.getStores();
               })
               .catch((e) => {
                  self.$Message["error"]({
                     background: true,
                     content: "حدث خطأ اثناء حفظ البيانات تأكد من المعلومات",
                  });
               });
         } else {
            self.$Message["error"]({
               background: true,
               content: "حدث خطأ اثناء حفظ البيانات تأكد من المعلومات",
            });
         }
      },
      cancel() {
         this.$Message["info"]({
            background: true,
            content: "تم الغاء الطلب",
         });
      },
   },
};
</script>

<style lang="scss" scoped></style>
